@charset "UTF-8";
#share-settings {
  display: flex;
  margin-top: 2em; }
  #share-settings ul.tabs {
    box-sizing: border-box;
    width: 150px;
    border-right: 1px solid rgba(0, 0, 0, 0.15);
    margin: 0;
    padding: 0; }
    #share-settings ul.tabs li {
      margin: 0; }
      #share-settings ul.tabs li.enabled a {
        position: relative;
        padding-left: calc(16px + 7px + .42em);
        color: #46b450; }
        #share-settings ul.tabs li.enabled a:before {
          content: "âœ“";
          text-align: center;
          position: absolute;
          top: 50%;
          left: 7px;
          transform: translate(0, -50%);
          width: 16px;
          height: 16px;
          background: none no-repeat scroll center center transparent;
          background-size: cover; }
        #share-settings ul.tabs li.enabled a[href="#tab_facebook"]:before {
          content: "";
          background: url(https://www.google.com/s2/favicons?domain=https://www.facebook.com/) no-repeat scroll center center transparent; }
        #share-settings ul.tabs li.enabled a[href="#tab_google"]:before {
          content: "";
          background: url(https://www.google.com/s2/favicons?domain=https://plus.google.com/) no-repeat scroll center center transparent; }
        #share-settings ul.tabs li.enabled a[href="#tab_linkedin"]:before {
          content: "";
          background: url(https://www.google.com/s2/favicons?domain=https://www.linkedin.com/) no-repeat scroll center center transparent; }
        #share-settings ul.tabs li.enabled a[href="#tab_pinterest"]:before {
          content: "";
          background: url(https://www.google.com/s2/favicons?domain=https://www.pinterest.com/) no-repeat scroll center center transparent; }
        #share-settings ul.tabs li.enabled a[href="#tab_twitter"]:before {
          content: "";
          background: url(https://www.google.com/s2/favicons?domain=https://twitter.com/) no-repeat scroll center center transparent; }
        #share-settings ul.tabs li.enabled a[href="#tab_whatsapp"]:before {
          content: "";
          background: url(https://www.google.com/s2/favicons?domain=https://www.whatsapp.com/) no-repeat scroll center center transparent; }
      #share-settings ul.tabs li + li {
        margin-top: -1px; }
    #share-settings ul.tabs a {
      display: block;
      text-decoration: none;
      padding: 7px;
      border: 1px solid transparent;
      margin-right: -1px; }
      #share-settings ul.tabs a:hover, #share-settings ul.tabs a.active {
        border-color: rgba(0, 0, 0, 0.15);
        border-right-color: #f1f1f1;
        background: linear-gradient(to right, #fff, transparent); }
      #share-settings ul.tabs a.active {
        color: #23282d; }
      #share-settings ul.tabs a.error {
        color: #dc3232; }
    #share-settings ul.tabs.ui-sortable li a {
      cursor: move; }
  #share-settings .panels {
    padding-left: 20px;
    flex-grow: 1;
    font-size: 13px;
    line-height: 1.5; }
    #share-settings .panels table thead td {
      padding: 0; }
    #share-settings .panels table thead div {
      padding: 1px 12px;
      background-color: #fff;
      border-left: 4px solid #dc3232;
      box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
      margin-bottom: 15px; }
    #share-settings .panels table thead p {
      margin: .5em 0;
      padding: 2px; }
    #share-settings .panels table:not(.active) {
      display: none; }
    #share-settings .panels ul.notes {
      padding-left: 1.14em;
      list-style: disc; }

#share__socials.ui-sortable {
  margin-left: -10px; }
  #share__socials.ui-sortable th {
    padding-left: 10px; }
  #share__socials.ui-sortable tr {
    cursor: move; }
    #share__socials.ui-sortable tr:hover {
      background-color: rgba(0, 0, 0, 0.05); }
